import { Component } from 'react';

const token = '9a0c090405d89a14fd4dc83c35bfff0bdcd39272';

export default class Issues extends Component {
  state = {
    issues: []
  };

  getIssues() {
    fetch('https://api.github.com/search/issues?q=davidgumzchoi', {
      headers: {
        Authorization: `token 9a0c090405d89a14fd4dc83c35bfff0bdcd39272`
      }
    })
      .then(response => response.json())
      .then(data => {
        console.log(data);
        this.setState({ issues: data.items });
      })
      .catch(error => console.error(error));
  }

  componentDidMount() {
    this.getIssues();
  }

  render() {
    const { issues } = this.state;
    return (
      <ul>
        {issues.map(issue => {
          <li>{issue}</li>;
        })}
      </ul>
    );
  }
}
